.Home {
  background-image: url('../../assets/home-bg.jpg');
  background-position: center bottom;
  background-size: cover;
  display: flex;
  align-items: center;
  height: 100vh;
  padding: 5rem;
}

.Title {
  font-size: 3.5rem;
}

.Articles {
  padding: 5rem;
}

.ArticlesGrid {
  display: flex;
  justify-content: space-between;
}

.Article {
  width: 400px;
}

.FormContainer {
  background-color: #d9d9d9;
  padding: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Form > h1 {
  text-transform: uppercase;
  letter-spacing: 0.3rem;
}

.FormNames {
  display: flex;
}

textarea {
  font-size: 16px !important;
}

.SendButton {
  margin-top: 0.2rem;
}

.HighlightVideo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5rem;
}

.HighlightVideo > h1 {
  text-transform: uppercase;
}
